

















import { Vue, Component, Prop } from 'vue-property-decorator';

@Component
export default class Modal extends Vue {
  // prop
  @Prop({ type: Boolean, default: true }) public showCloser: any;
  @Prop({ type: String, default: '' }) public classContainer: any;

  public mounted() {
    window.addEventListener('keydown', this.close);

    const [body] = document.getElementsByTagName('body');
    body.insertBefore(this.$el, body.firstChild);
  }

  public close(e: any): void {
    if (e.keyCode === 27) {
      window.removeEventListener('keydown', this.close);
      this.$emit('close');
    }
  }

  public close2(e: any): void {
    window.removeEventListener('keydown', this.close);
    this.$emit('close');
  }

  public beforeDestroy() {
    const [parent] = document.getElementsByTagName('body');
    if (parent && this.$el && this.$el.parentNode === parent) {
      parent.removeChild(this.$el);
    }
  }
}
